.options {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  &__error{
    color: #F97B7C;
	  font-size: 16px;
  }

  &.wide {
    text-align: center;
    .options__item {
      width: 100%;
      margin-right: 0;
    }
  }

  &--two-val {
    .options__item {
      width: 48%;
    }
  }

  &--three-val {
    .options__item {
      width: 31%;
    }
  }

  &--with-img {
    .options__item {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 60px;
      border: 1px solid $c-border;
      
      &.is-active {
        border: 1px solid $c-primary;
      }

      &:hover {

      }
    }
  }

  &--white {
    .options__item {
      border: 1px solid $c-border;
      
      &.is-active {
        color: #fff;
        background-color: $c-primary;
        border: 1px solid $c-primary;
      }

      &:hover {
        &:not(.is-active) {
          background-color: $c-l-gray;
        }
      }

    }
  }

  &--black {
    .options__item {
      color: #fff;
      border: 1px solid rgba(255, 255, 255, 0.3);
      
      &.is-active {
        border: 1px solid rgba(255,255,255, 1);
      }

      &:hover {
        &:not(.is-active) {
          background-color: rgba(255,255,255, .05);
        }
      }
    }
  }

  &--white, &--black {
    .options__item {
      padding: 17px 5px;
    }
  }

  &__item {
    text-align: center;
    user-select: none;
    cursor: pointer;
    border-radius: $baseRadius;
    line-height: 1;
    font-family: $f-exo2;
    font-weight: 700;
    font-size: $fz-middle;
    text-transform: uppercase;
    min-width: 80px;
    margin-bottom: 10px;

    &:not(:last-child) {
      margin-right: 10px;
    }

    &.is-active {

      // for programs
      &.g-mather {
        background-image: $gradient-mather;
        border: 1px solid #fff;
      }
      &.g-hero {
        background-image: $gradient-hero;
        border: 1px solid #fff;
      }
      &.g-extreme {
        background-image: $gradient-extreme;
        border: 1px solid #fff;
      }
      &.g-tomorrow {
        background-image: $gradient-tomorrow;
        border: 1px solid #fff;
      }
    }
  }
}





