/* ===========================
   App theme
   =========================== */

@charset "UTF-8";


// Global Vars

$bgImgPath: '../img';

$headerHeight: 54px;
$mobMenuHeight: 50px;
$maxContentWidth: 1200px;
$baseMargin: 30px;

$baseShadow: 0 6px 10px rgba(31, 68, 123, 0.03);
$middleShadow: 0 12px 44px rgba(31, 68, 123, 0.13);
$baseRadius: 5px;
$bigRadius: 18px;


// -- Typography
// Fonts
@import url('https://fonts.googleapis.com/css?family=Exo+2:200,700&subset=cyrillic');
@import url('https://fonts.googleapis.com/css?family=Roboto:400,700&subset=cyrillic');

$f-exo2: 'Exo 2', Arial, Tahoma, sans-serif;
$f-roboto: 'Roboto', Arial, Tahoma, sans-serif;

// Headers
$fz-big: 3.0rem;
$fz-h1: 2.2rem;
$fz-h2: 2.0rem;
$fz-h3: 1.8rem;

// Base fonts
$fz-base: 1.5rem;
$fz-middle: 1.2rem;
$fz-small: 1.1rem;


// -- Colors

// Font color
$c-text: #1F447B;
$c-text-gray: #C8C9CF;

// Main color
$c-primary: #70CFFE;
$c-secondary: #1F447B;
$c-border: #E4E7F4;

// Button colors
$btn-primary: #56DAA1;
$btn-secondary: #70CFFE;
$btn-action: #FF8858;

// Other Colors
$c-green: #56DAA1;
$c-red: #F97B7C;
$c-orange: #FF6F37;
$c-l-blue: #869BB8;
$c-l-yellow: #fffbca;
$c-s-yellow: #928227;

// Gray colors
$c-gray: #F9FBFC;
$c-l-gray: #F3F6F8;
$c-m-gray: #b3b9bd;
$c-s-gray: #696D7D;

// Program Color - text highlight
$c-highlight-1: #E8FFF5;
$c-highlight-2: #FFF6F2;
$c-highlight-3: #E7F6FF;
$c-highlight-4: #F7F3FF;

// Gradient Program Todayme
$gradient-mather: radial-gradient(at 70% 120%, #FFE799 -14%, #42B0BA 72%);
$gradient-hero: radial-gradient(at 70% 120%, #762FB3 3%, #261DB4 100%);
$gradient-extreme: linear-gradient(-44deg, #5BFFE2 0%, #005FFF 51%, #EE0652 100%, #FF0045 100%);
$gradient-tomorrow: radial-gradient(at 70% 120%, #EF9227 0%, #E5522B 83%);
