
.notify {

  &--content {
    .notify__item {
      padding: 20px;
      border-radius: $baseRadius;
      margin-bottom: 20px;
    }
  }

  &--form {
    .notify__item {
      margin-bottom: 2px;

      @include av-mq(portable) {
        padding: 15px 15px;
        // margin: 0 15px 10px;
      }
      @include av-mq(desk) {
        padding: 20px 80px;
      }
    }
  }

  &__item {
    position: relative;

    &--alert {
      background-color: $c-red;
      color: #fff;
      a {color: #f9ff47;}
      .notify__close {
        fill: #fff;
      }
    }

    &--warning {
      background-color: #FFFBCA;
      color: #928227;
      .notify__close {
        fill: #928227;
      }
    }

    &--success {
      background-color: $c-green;
      color: #fff;
      a {color: #f9ff47;}
      .notify__close {
        fill: #fff;
      }
    }
  }

  &__title {
    font-size: $fz-h3;
    margin-bottom: 10px;
  }

  &__desc {
    position: relative;
    padding-right: 20px;
    line-height: 1.4;
    @include av-mq(portable) {
      .notify__num {
        margin-right: 10px;
      }
      display: flex;
      align-items: center;
    }
  }

  &__num {
    font-size: $fz-big;
    font-family: $f-exo2;
    font-weight: 700;
    text-align: right;
    line-height: 1;

    @include av-mq(desk) {
      position: absolute;
      left: -30px; top: 50%;
      transform: translateY(-50%);
      margin-top: -3px;
    }
  }

  &__desc-sub {

  }

  &__close {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px; height: 30px;
    cursor: pointer;
    position: absolute;
    right: 10px; top: 50%;
    transform: translatey(-50%);
  }

}