.chat {
  box-shadow: $baseShadow;
  background-color: #fff;

  &__inner-block {
    margin-bottom: $baseMargin;
    border-radius: $baseRadius;

    width: 100%;
    height: 650px;

    display: flex;
    flex-flow: column;
    justify-content: space-between;
  }

  &__close {
    top: -46px;
    right: 10px;
    width: 30px;
    height: 30px;
    position: absolute;
    display: none;

    cursor: pointer;
  }

  &__admin-panel {
    display: flex;

    &-button {
      width: 100%;
      flex-basis: 100%;
      border-radius: 0;
    }
  }

  &__emoji {
    left: 0;
    bottom: 50px;
    position: absolute;

    &_hidden {
      display: none;
    }
  }

  &__emoji-button {
    width: 40px;
    border: none;
    cursor: pointer;
    border-right: 1px solid #E4E7F4;
    background: transparent;
  }

  // Window mode modifier

  &_window {
    width: 50%;

    left: 25%;
    bottom: -1px;
    position: fixed;

    border: 1px solid $c-l-blue;
  }

  &_window &__inner-block {
    margin-bottom: 0;
  }

  &_window &__close {
    display: block;
  }

  // Forwarding modifier

  &_forwarding &-form {
    margin: 0;
  }

  &_forwarding &__emoji-button, &_forwarding &__emoji {
    display: none;
  }

  &_forwarding &-form__inner {
    padding: 0;
    flex-wrap: wrap;
  }

  &_forwarding &__button-waiting {
    display: none;
  }

  &_forwarding &-form__field {
    width: 100%;
    height: 300px;
  }

  &_forwarding .btn-chat {
    width: 100%;
    display: block;
    position: relative;
  }

  // Fetching modifier

  &_fetching &__spinner {
    display: flex;
  }

  &_fetching &-content, &_fetching &-form {
    //display: none;
  }

  &__spinner {
    display: none;
    flex: 1 1 auto;
    align-items: center;
    justify-content: center;
  }

  &__tabs {
    display: flex;
    min-height: 59px;
    border-radius: $baseRadius $baseRadius 0 0;
    border-bottom: 1px solid $c-border;
    // overflow: hidden;
  }

  &__tab {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    border: 1px solid transparent;
    height: 58px;
    cursor: pointer;
    &:first-child {
      border-radius: $baseRadius 0 0 0;
    }
    &:last-child {
      border-radius: 0 $baseRadius 0 0;
    }
    
    &--admin {
	    width:100%;
	    cursor: auto;
    }

    &--active {
      color: $c-primary;
      border-top: 1px solid $c-border;
      border-left: 1px solid $c-border;
      border-right: 1px solid $c-border;
      background-color: #f7f9fb;
      // &:hover {
      //   background-color: #fff;
      // }
    }

    &--disabled {
      color: $c-gray;
      cursor: not-allowed;
      background-color: #c4c4c4;
      border-top: 1px solid $c-border;
      border-left: 1px solid $c-border;
      border-right: 1px solid $c-border;
      // &:hover {
      //   background-color: #fff;
      // }
    }

    // &:hover {
    //   &:not(.chat__tab--active) {
    //     background-color: #f7f9fb;
    //   }
    // }

  }
  &__tab-title {

  }
  &__tab-msg {
    font-size: $fz-middle;
    color: $c-primary;
    margin-left: 5px;
    font-weight: 700;
  }
}

.chat-info {
  background-color: #f7f9fb;
  border-bottom: 1px solid $c-border;
  &__inner {
    padding: 14px 40px;
    display: flex;
  }
  &__ava {
    width: 30px;
    height: 30px;
    margin-right: 15px;
    img {
      max-width: 100%;
    }
  }
  &__ava-status {

  }
  &__user {

  }
  &__user-name {
    font-size: $fz-base;
    font-weight: 700;
    span {
      font-size: 1.2rem;
      color: $c-l-blue;
      font-weight: 400;
      line-height: 1;
    }
  }
  &__user-status {
    color: $c-l-blue;
    font-size: $fz-middle;
  }
}

.chat-content {
  flex: 1 1 auto;
  flex-direction: column-reverse;
  overflow-y: auto;
  //overflow-x: hidden;
  background: white;
	height:505px;
  display: none;

	&.active{
		display:block;
	}
}

.chat-messages {
  width: 100%;

  @include av-mq(desk) {
    padding: 40px;
  }
  @include av-mq(portable) {
    padding: 20px;
  }

  &__date {
    display: block;
    margin: $baseMargin auto $baseMargin;
    text-align: center;
    width: 80%;
    height: 1px;
    background-color: $c-border;
    span {
      display: inline-block;
      position: relative;
      top: -9px;
      background-color: #fff;
      padding: 0 10px;
      font-size: $fz-middle;
      color: $c-s-gray;
    }
  }
}

.chat-msg {
  display: flex;
  align-items: flex-start;
  &:not(:last-child) {
    margin-bottom: 20px;
  }

  &--someone {
    justify-content: flex-start;

    .chat-msg__ava {
      margin-right: 4%;
    }

    .chat-msg__text {
      background-color: #EFF1F2;
      color: $c-s-gray;
      border-radius: 0 12px 12px 12px;

      &:before {
        left: -9px;
        top: 0;
        border-width: 0 0 9px 9px;
        border-color: transparent transparent #E3E7EA transparent;
      }

    }
  }

  &--you {
    justify-content: flex-end;

    .chat-msg__ava {
      margin-left: 4%;
    }

    .chat-msg__text {
      background-color: $c-green;
      color: #fff;
      border-radius: 12px 0 12px 12px;

      &:before {
        right: -9px;
        top: 0;
        border-width: 9px 0 0 9px;
        border-color: transparent transparent transparent #4DC793;
      }

    }

  }

  &--system {

    .chat-msg__text {
      color: $c-s-yellow;
      background-color: $c-l-yellow;
    }

    .chat-msg__text {
      &:before {
        border-color: transparent transparent transparent $c-s-yellow;
      }
    }

  }

  &__ava {
    flex-shrink: 0;
    width: 46px;
    height: 46px;
    img {
      max-width: 100%;
    }
  }
  &__content {

  }
  &__name{
    font-size: $fz-middle;
    color: $c-s-gray;
    margin-bottom: 4px;
  }

	&__time{
		line-height: 13px;
		font-size: $fz-middle;
		color: $c-s-gray;
	}

  &__text {
    position: relative;
    display: inline-block;
    padding: 14px;
    line-height: 1.3;
    &:before {
      content: '';
      display: block;
      position: absolute;
      width: 0;
      height: 0;
      border-style: solid;
    }
  }
}

.chat-form {
  margin: 0 40px 0 40px;
  padding-bottom: 40px;
  @include av-mq(portable) {
    margin: 0 20px 0 20px;
    padding-bottom: 20px;
  }

  &__inner {
    display: flex;
    position: relative;
    border: 1px solid $c-border;
    @include av-mq(desk) {
      padding-right: 120px;
      border-radius: $baseRadius;
    }
    @include av-mq(portable) {
      padding-right: 50px;
      border-radius: 30px;
    }
  }
  &__field {
    border: none;
    height: 44px !important;
    padding: 10px 12px;
    background-color: transparent;
  }
}

.btn-chat {
  user-select: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: -1px;
  top: -1px;
  background-color: $btn-primary;
  z-index: 1;
  border-radius: $baseRadius;
  height: 46px;
  min-width: 46px;
  font-family: $f-exo2;
  font-size: $fz-middle;
  font-weight: 700;
  text-transform: uppercase;

  &:hover {
    .no-touchevents & {
      &:hover {
        background-color: lighten($btn-primary, 6%);
      }
      &:active {
        // background-color: darken($btn-primary, 3%);
      }
    }
  }

  @include av-mq(desk) {
    .btn-chat__title {
      padding: 15px 20px;
      display: inline-block;
    }
  }
  @include av-mq(portable) {
    border-radius: 50%;
    .btn-chat__title {
      display: none;
    }
    .btn-chat__ico {
      display: inline-block;
    }
  }
  &__title {
    color: #fff;
    line-height: 1;
  }
  &__ico {
    display: none;
  }
}