/* ===========================
   App layout
   =========================== */

.layout {
  height: 100%;
  background-color: $c-gray;

  &--entry {
    background-color: #fff;
  }

  &__inner {
    margin: 0 auto;
    max-width: $maxContentWidth;
    padding: 20px 20px 0;

    &--profile {
      max-width: 800px;
    }
    
    @include av-mq(portable) {
      padding: 0 0 $mobMenuHeight 0;
    }
  }

  &__menu {

  }

  &__menu-nav {
    @include av-mq(portable) {
      display: none;
    }
  }

  &__calendar {

  }

  &__menu-inner {
    &.is-fixed {
      z-index: 10;
      position: fixed;
      top: 20px;
    }
  }

  &__content {
    
  }
}

.mobile-picker{
	overflow-y: hidden;
	position: relative;
}